import React from "react";
import Map from '../map'
import { connect } from 'react-redux'
import Player from "../player";
import DisplayLevel from './DisplayLevel'

import createDungeon from "../../data/maps/2";
import store from "../../config/store";
import Player2 from "../player2";
import Inventory from "../inventory";


//the createdungeon function draws a new map and loads it to the tiles

function World(props) {
  store.dispatch({
    type: "ADD_TILES",
    payload: {
      tiles: createDungeon()
    }
  });

  return (
    <div>
      <div style={{
        position:'fixed',
        width: '15%'
      }}>
      <DisplayLevel />
      <Inventory />
      </div>
      <div
        style={{
          position: "relative",
          width: "3000px",
          height: "1800px",
          margin: "20px auto"
        }}
      >      
        <Map />
        <Player />
        <Player2 />
      </div>
    </div>
  );
}

export default World
