import React from 'react'
import { connect } from 'react-redux'
import item from '../../tiles/item2.png'

function Inventory(props) {
  return (
    <div style={{
      color:'white',
      width: '50px',
      height: '500px',
      }}>
    <div className="grid-container" style={{
      gridRowGap: '20px',
      width: '50px',
      height: '500px',
    }}>
      <div className="grid-item">
        <div style={{
        border: '1px solid white',
        backgroundImage: props.items[0] ? `url('${item}')` : null,
        width: '50px',
        height: '50px',
        backgroundColor: 'brown',
      }}/>
      
      </div>
      <div className="grid-item">
        <div style={{
        border: '1px solid white',
        width: '50px',
        height: '50px',
        backgroundColor: 'red',
      }}/>
      </div>
      <div className="grid-item">
        <div style={{
        border: '1px solid white',
        width: '50px',
        height: '50px',
        backgroundColor: 'brown',
      }}/>
      </div>
      <div className="grid-item">
        <div style={{
        border: '1px solid white',
        width: '50px',
        height: '50px',
        backgroundColor: 'red',
      }}/>
      </div>
      <div className="grid-item">
        <div style={{
        border: '1px solid white',
        width: '50px',
        height: '50px',
        backgroundColor: 'brown',
      }}/>
      </div>
      <div className="grid-item">
        <div style={{
        border: '1px solid white',
        width: '50px',
        height: '50px',
        backgroundColor: 'red',
      }}/>
      </div>
      <div className="grid-item">
        <div style={{
        border: '1px solid white',
        width: '50px',
        height: '50px',
        backgroundColor: 'brown',
      }}/>
      </div>
      <div className="grid-item">
        <div style={{
        border: '1px solid white',
        width: '50px',
        height: '50px',
        backgroundColor: 'red',
      }}/>
      </div>
      <div className="grid-item">
        <div style={{
        border: '1px solid white',
        width: '50px',
        height: '50px',
        backgroundColor: 'brown',
      }}/>
      </div>
      <div className="grid-item">
        <div style={{
        border: '1px solid white',
        backgroundImage: `url('${item}')`,
        width: '50px',
        height: '50px',
        backgroundColor: 'red',
      }}/>
      </div>
  </div>
  </div> 
  )
}

function mapStateToProps(state) {
  return {
    ...state.inventory,
  }
}

export default connect(mapStateToProps)(Inventory)