import React from 'react'
import { connect } from 'react-redux'

function DisplayLevel(props) {
  return (
  <h3 style={{color:'white'}}>Dungeon Level: {props.level}<br />
  Move with the arrow keys<br /> 
  Operate stairs with "." <br />
  Space to interact</h3>
  )
}

function mapStateToProps(state) {
  return {
    ...state.map,
  }
}

export default connect(mapStateToProps)(DisplayLevel)